// It indirectly affects all other heading  font sizes as well by serving as the
// upper limit to our scale.
//
// Styleguide Configuration.Headings.Maximum Heading Size (Mobile)
//
// Access: Public
//
// Since: 2.0.0

// Maximum Heading Size (Mobile)
